import HomeView from "@/views/HomeView.vue";
import AboutView from "@/views/AboutView.vue";
import NewView from "@/views/NewView.vue";
import AxiosView from "@/views/AxiosView.vue";
import SignUpView from "@/views/SignUpView.vue";
import LoginView from "@/views/LoginView.vue";
import DeleteUserView from "@/views/DeleteUserView.vue";

export default {
  namespaced: true,

  state: {
    title: "",

    basePath: "",

    list: {
      home: {
        path: `/home`,
        name: `home`,
        meta: {
          title: `Home`,
          icon: `home`,
        },
        component: HomeView,
      },

      about: {
        path: `/about`,
        name: `about`,
        meta: {
          title: `About`,
          icon: `question_mark`,
        },
        component: AboutView,
      },
      new: {
        path: `/new`,
        name: `new`,
        meta: {
          title: `New`,
          icon: `event`,
        },
        component: NewView,
      },
      axios: {
        path: `/axios`,
        name: `axios`,
        meta: {
          title: `Axios`,
          icon: `search`,
        },
        component: AxiosView,
      },
      signup: {
        path: `/signup`,
        name: `signup`,
        meta: {
          title: `Signup`,
          icon: `edit`,
        },
        component: SignUpView,
      },
      login: {
        path: `/login`,
        name: `login`,
        meta: {
          title: `Login`,
          icon: `login`,
        },
        component: LoginView,
      },
      deleteuser: {
        path: `/delete`,
        name: `delete`,
        meta: {
          title: `DeleteUser`,
          icon: `delete`,
        },
        component: DeleteUserView,
      },
    },

    // visible: {
    //   header: true,
    //   navi: true,
    //   footer: true,
    // },
  },

  getters: {
    title(state) {
      return state.title;
    },

    basePath(state) {
      return state.basePath;
    },

    menuList(state) {
      return state.list;
    },

    // visible(state) {
    //   return state.visible;
    // },
  },

  mutations: {
    setTitle(state, title) {
      state.title = title;
    },

    // setVisible(state, { key, value }) {
    //   state[key] = value;
    // },
  },

  actions: {
    setTitle({ commit }, title) {
      commit("setTitle", title);
    },

    // setVisible({ commit }, payload) {
    //   commit("setVisible", payload);
    // },
  },
};
